import React, { useState } from 'react';
import { asset } from '../util/util'
import { Link } from 'gatsby';
import LazyImage from './LazyImage';
import Skeleton from "react-loading-skeleton";

const Preloader = () => {
  return (
    <>
      <div style={{ marginBottom: "15px" }}>
        <Skeleton width="100%" height="190px" style={{ marginBottom: "15px" }} />
      </div>
      <div style={{ marginBottom: "5px" }}>
        <Skeleton width="90%" height={20} />
      </div>
      <div>
        <Skeleton width="70%" height={20} />
      </div>
    </>
  )
}

const Template = ({ work }) => {
  const [loading, setLoading] = useState(true);

  const styleHidden = {
    visibility: "hidden",
    width: 0,
    height: 0
  };

  return (
    <>
      {loading && <Preloader />}
      <div className="work" style={loading ? styleHidden : {}}>
        <Link to={work.link} className="work__link">
          <LazyImage
            className="work__image"
            src={work.preview}
            alt={work.title}
            loading={loading}
            setLoading={setLoading} />
        </Link>
        <div className="work__title">{work.title}</div>
        <div className="work__description">
          {work.subtitle}
        </div>
      </div>
    </>
  );
}

const Works = ({ data, wrapperClass = "row works" }) => {

  return (
    <div className={wrapperClass}>
      {
        data.map((work) => {
          return (
            <div key={work.id} className="col-md-4 col-sm-6 mb-md-5 mb-3">
              <div className="work">
                <Template work={work} />
              </div>
            </div>
          )
        })
      }
    </div>
  )
}

export default Works;